import Top from './Top';
import Cast from './Cast';
import Crew from './Crew';
import { useScreenWidth } from '../../useScreenWidth';
import { DetailTile } from '../../common/DetailTile';
import { Container } from '../../common/Container';
import { usePopularMovies } from '../MovieList/usePopularMovies';
import { useGenres } from '../../useGenres';
import { useMoviesDetails } from './useMoviesId';

const MoviePage = () => {
    const screenWidth = useScreenWidth();
    const { popularMovies } = usePopularMovies();
    const movie_list = popularMovies.data;
    const { genres } = useGenres();
    const genre_list = genres.data;
    const moviesDetails = useMoviesDetails();
    const detalis = moviesDetails.data;

    return (
        <>
            {popularMovies.status === "success" ? (
                <>
                    <Top />
                    <Container>
                        {movie_list.map(movie => (
                            <DetailTile
                                key={movie.id}
                                image={movie.poster_path}
                                title={movie.title}
                                year={movie.release_date}
                                infotitle="Production:"
                                infoSubtitle="Release date:"
                                contentTitle={
                                    movie.title?.map((title) =>
                                    detalis?.find((country) =>
                                    (screenWidth > 767 ? country.production_countries.name
                                        : country.production_countries.iso_3166_1).id = title)
                                    )}
                                contentSubtitle={movie.release_date}
                                genres={
                                    movie.genre_ids.map((number) =>
                                        genre_list?.find((item) =>
                                            item.id === number).name
                                    )
                                }
                                rating={movie.vote_average}
                                votes={movie.vote_count}
                                description={movie.overview}
                            />
                        ))}
                        <Cast />
                        <Crew />
                    </Container>
                </>
            )
                : null
            }
        </>
    )
};

export default MoviePage;