import axios from "axios";
import { useState, useEffect } from "react";
import { movie_details } from "../../movies/api";

export const useMoviesDetails = () => {
    const [moviesDetails, setMovieDetalis] = useState("");

    useEffect(() => {
        const getMovieDetalis = async () => {
            try {
                const response = await axios.get(`${movie_details}`);
                setMovieDetalis({
                    status: "success",
                    data: response.data.results,
                });
            } catch (error) {
                setMovieDetalis({
                    status: "error"
                });
                console.log(error);
            }
        };

        setTimeout(getMovieDetalis, 0);
    }, []);

    return moviesDetails;
};