import {
    Content,
    Description,
    Image,
    Info,
    Opinion,
    Production,
    Rating,
    Tag,
    Tags,
    Ten,
    Text,
    Title,
    Vector,
    Votes,
    Wrapper,
    Year
} from './styled';
import star from '../../images/star.svg';
import { useScreenWidth } from '../../useScreenWidth';

export const DetailTile = ({
    image,
    title,
    year,
    infotitle,
    infoSubtitle,
    contentTitle,
    contentSubtitle,
    genres,
    rating,
    votes,
    description
}) => {
    const date = new Date(contentSubtitle).toLocaleDateString();

    return (
        <Wrapper>
            <Image src={"https://image.tmdb.org/t/p/" + (useScreenWidth > 767 ? "w342/" : "w154/") + image} />
            <Content>
                <Title>{title}</Title>
                <Year>{new Date(year).getFullYear()}</Year>
                <Production>
                    <div><Info>{infotitle}</Info>{contentTitle}</div>
                    <div><Info>{infoSubtitle}</Info>{date}</div>
                </Production>
                <Tags>
                    {genres ? genres.map((genre) =>
                        <Tag key={genre}>{genre}</Tag>
                    ) : ""}
                </Tags>
                <Opinion>
                    <Rating>
                        <Vector src={star} />
                        <Text>{rating.toLocaleString("pl-PL", {
                            minimumFractionDigits: 1,
                            maximumFractionDigits: 1,
                        })}</Text>
                    </Rating>
                    <Ten>/ 10</Ten>
                    <Votes>{votes.toLocaleString('pl-PL', {
                        useGrouping: true,
                    })} votes</Votes>
                </Opinion>
                <Description>{description}</Description>
            </Content>
        </Wrapper>
    );
};